import Vue from 'vue'
import Router from 'vue-router'
import User from '../components/user'
import Health from '../components/health'
import Food from '../components/food'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/food',
      name: 'food',
      component: Food,
    }, {
      path: '/',
      name: 'health',
      component: Health,
    }, {
      path: '/user',
      name: 'user',
      component: User,
    }
  ]
})



