<script>
  import './style/style.css'
  import './style/common.css'
  import Vue from 'vue'
  import Echarts from 'echarts'

  Vue.use(Echarts);
  export default {
    name: 'health',
    data() {
      return {
        dinningDay: 28,
        maximum: "1月16日",
        mealAmount: "50%",
        calorieAmount: "50%",
        award: '米饭达人',
        echartsOption: {
          tooltip : {
            trigger: 'axis'
          },
          grid: {
            left: 30,
            right:20,
            top:30,
            bottom:30
          },
          legend: {
            // /* y: 'bottom',*/
            x:"right"
          },
          calculable : true,
          xAxis : [
            {
              type : 'category',
              axisLabel: {
                color: "#194296"  //刻度线标签颜色
              },
              boundaryGap : false,
              data : ['1/15','1/16','1/17','1/18','1/19','1/20', '1/21']
            }
