            }
          ],
          series : [
            {
              type: 'line',
              color: '#ff9736',
              smooth: true,//设置折线图平滑
              data: [200, 400, 700, 500, 400, 800, 600]
            }
          ]
        }
      }
    },
    mounted() {
      // this.chart = Echarts.init(document.getElementById(id))
      this.drawLine("chartSpline")

    },
    methods: {
      drawLine(id) {
        console.log(document.getElementById(id))
        this.chart = Echarts.init(document.getElementById(id))
        this.chart.setOption(this.echartsOption)
      }
    }
  }
