    z-index: 999;
  }

  .alert-content {
    position: absolute;
    top: 30%;
    left: 10%;
    width: 80%;
    font-size: 14px;
    text-align: center;
    letter-spacing: 1px;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 15px;
  }

